import React from 'react'
import { BarChart, Bar, XAxis, YAxis, Tooltip, Legend, ResponsiveContainer } from 'recharts'


export default function SkillBarChart({ data }: { data: any[] }) {
// Aggregate average skill per class (or overall)
const avg = (key: string) => Math.round((data.reduce((s, r) => s + r[key], 0) / data.length) * 10) / 10
const prepared = [
{ skill: 'Comprehension', value: avg('comprehension') },
{ skill: 'Attention', value: avg('attention') },
{ skill: 'Focus', value: avg('focus') },
{ skill: 'Retention', value: avg('retention') },
]


return (
<div style={{ width: '100%', height: 300 }}>
<ResponsiveContainer>
<BarChart data={prepared} margin={{ top: 20, right: 20, left: 0, bottom: 5 }}>
<XAxis dataKey="skill" />
<YAxis />
<Tooltip />
<Legend />
<Bar dataKey="value" name="Avg score" />
</BarChart>
</ResponsiveContainer>
</div>
)
}