import React from 'react'
import { Radar, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, ResponsiveContainer } from 'recharts'


export default function PersonaRadar({ student }: { student: any }) {
if (!student) return null
const data = [
{ subject: 'Comprehension', A: student.comprehension },
{ subject: 'Attention', A: student.attention },
{ subject: 'Focus', A: student.focus },
{ subject: 'Retention', A: student.retention },
{ subject: 'Engagement', A: Math.round(student.engagement_time / 10) },
]


return (
<div style={{ width: '100%', height: 280 }}>
<ResponsiveContainer>
<RadarChart outerRadius={90} data={data}>
<PolarGrid />
<PolarAngleAxis dataKey="subject" />
<PolarRadiusAxis />
<Radar name={student.name} dataKey="A" fill="#82ca9d" fillOpacity={0.6} />
</RadarChart>
</ResponsiveContainer>
</div>
)
}